    .buttonStyle{
      display: inline-block;
    position: absolute;
    top: 0px;
    left: 0;
    margin: 12px;
  
    color: #ffffff;
    z-index: 1 !important;
    padding: 6px;
    font-weight: bold;
    }

    @media only screen and (max-width: 768px) {
        .buttonStyle{
            display: inline-block;
          position: absolute;
          top: 0px;
          left: 0;
          margin: 12px;
          margin-left: 5px;
        
          color: #ffffff;
          z-index: 1 !important;
          padding: 6px;
          font-weight: bold;
          }

    }
     
    .mapContainer {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    }